var weatherTypeAomenConevrt = (code) => {
	//console.log("curr lang ==>"+vm.$i18n.locale)
	var value=vm.$i18n.locale;
	var name="";
	code=""+code;
	for(var i=0;i<watypeList.length;i++){
		if(code.trim()==watypeList[i].code||"0"+code.trim()==watypeList[i].code){
			if(value=='zh-CN'){
				name=watypeList[i].name;
			}else if(value=='en-US'){
				name=watypeList[i].name_en;
			}else if(value=='pt-PT'){
				name=watypeList[i].name_pt;
			}
			break;
		}
	}
	return name;
}
// 天气转换
var weatherType2IconSm = (type) => {
  let classType = [];
  for (var i = 0; i < type.length; i++) {
    for (var j = 0; j < wtType.length; j++) {
      if (type[i].ja == wtType[j].ja) {
        classType.push(wtType[j])

      }

    }
  }
  return classType;
}
// 天气转换
var weatherType2IconSmfb = (type) => {
    let classType = [];
    for (var i = 0; i < type.length; i++) {
      for (var j = 0; j < wtType.length; j++) {
        if (type[i].fb == wtType[j].ja) {
          classType.push(wtType[j])
  
        }
  
      }
    }
    return classType;
  }
  // 天气转换
var weatherType2IconSmfa = (type) => {
    let classType = [];
    for (var i = 0; i < type.length; i++) {
      for (var j = 0; j < wtType.length; j++) {
        if (type[i].fa == wtType[j].ja) {
          classType.push(wtType[j])
  
        }
  
      }
    }
    return classType;
  }
// 实况天气转换
  var observe = (type) => {
    let namestr="";
    for (var i = 0; i < wtType.length; i++) {
      if (type.l5 == wtType[i].ja) {
        namestr=wtType[i];

      }
    }
    return namestr;
  }
  // 实况天气转换主頁用
 var getWeatherbyNum= (type) => {
 	 var namestr = "";
      for (var i = 0; i < wtType.length; i++) {
        if (wtType[i].ja == type) {
          namestr = wtType[i].name + "_" + wtType[i].icon;
          break;
        }
      }
      return namestr;
 }
// 时间转换办法
var timeconversion = (type) => {
  let weathertime = [];
  for (var i = 0; i < type.length; i++) {
    weathertime.push(type[i].substring(8, 10));
  }
  for (var i = 0; i < weathertime.length; i++) {
    if (weathertime[i].substring(0, 1) == '0') {
      weathertime[i] = weathertime[i].substring(1, 2);
    }
  }
  return weathertime;
}

export default {
  install: function (Vue) {
    Vue.prototype.weatherType2IconSm = weatherType2IconSm;
    Vue.prototype.weatherType2IconSmfa = weatherType2IconSmfa;
    Vue.prototype.weatherType2IconSmfb = weatherType2IconSmfb;
    Vue.prototype.timeconversion = timeconversion;
    Vue.prototype.observe = observe;
    Vue.prototype.getWeatherbyNum = getWeatherbyNum;
    Vue.prototype.weatherTypeAomenConevrt=weatherTypeAomenConevrt;
  }
}



