var commonAjax = (qtype,qurl,param,sfack,efack,async) =>{
	if(async==undefined||async==''){
		async=false
	}
	//console.log("async==>"+async)
	$.ajax({
			type: qtype,
			url: qurl,
			data: param,
			dataType: "json",
			crossDomain:true,//是否强制跨域
			timeout:10000,//请求超时
			async: async,//是否异步
			success:sfack,
			error:efack,
			complete : function(XMLHttpRequest,status){
				if(status=='timeout'){//超时,status还有success,error等值的情况
					window.vm.popshow=false;//遮罩層關閉
					window.vm.$vux.loading.hide();//關閉正在加載
					// 显示
					window.vm.$vux.toast.show({
						type:"cancel",
						text: window.vm.$t('requestTimeout')
					})
			　　}else if(status=='error'){
					window.vm.popshow=false;//遮罩層關閉
					window.vm.$vux.loading.hide();//關閉正在加載
					// 显示
					window.vm.$vux.toast.show({
						type:"cancel",
						text: window.vm.$t('nodata')
					})
				}
			}
	});
}
var isAndroid_ios = () => {
		//android is true else is false
		var u = navigator.userAgent,
			app = navigator.appVersion;
		var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //android终端或者uc浏览器  
		var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端  
		return isAndroid == true ? true : false;
}
var showBottomToast= (val)=>{
		// 显示
		//window.vm.$vux.toast.text(val, 'bottom');
		try{
			window.android.showToast(val)
		}catch(e){
			console.log("show toast");
			//TODO handle the exception
		}
		
}
var setI18nValue = (i18nStr) =>{
	 window.vm.$i18n.locale = i18nStr;
	 //showBottomToast("setValue==>"+i18nStr)
}
var jumpQuery = () =>{
	var hrefstr=location.href;
	hrefstr=hrefstr.split("#")[0];
	var jumpstr="/query/query";
	jumpstr=hrefstr+"#"+jumpstr;
	console.log("param==>"+window.vm.$t('queryHelp')+"==>"+jumpstr);
	window.android.openZoomActivity(window.vm.$t('queryHelp'),jumpstr)
}

export default {
    install: function(Vue) {
        Vue.prototype.$myConfig = config;
        Vue.prototype.$commonAjax = commonAjax;
        Vue.prototype.$isAndroid = isAndroid_ios;
		Vue.prototype.$showBottomToast = showBottomToast;
		Vue.prototype.$setI18nValue = setI18nValue;
		Vue.prototype.$jumpQuery = jumpQuery;

    }
}


