export default {
  data() {
    return {
  
    };
  },
  props: ["title"],
  methods: {
    goBack() {
      var backUrl=this.title.backUrl;
      console.log("param===>come in"+backUrl)
      console.log("title==>"+backUrl)
			this.$vux.loading.hide()
      if(backUrl!=undefined&&backUrl!=null&&backUrl!='null'&&backUrl!=''){
      	  if(backUrl=="/index"){
      	  	console.log("param===>come in end")
						if(this.$isAndroid()){
							window.android.goBack();
						}else{
							//window.location.href="popOut";
						}
      	  	/*window.android.destory();*/
      	  	//this.$router.go(-1); //返回上一次历史记录
      	  	
      	  }else{
      	  	 this.$router.push(backUrl);
      	  }
					if(this.$isAndroid()){
							window.android.stopSpeakVoice()
						}
      }else{
      	console.log("param===>not come in")
      	 this.$router.go(-1); //返回上一次历史记录
      }
    },
    gother() {
      if(this.title.flag){
        this.$router.push("/other"); //其他区域
      }
    }
  },mounted:function () {
	window.goBack = this.goBack;//返回
  }
};



