

import Vue from 'vue'
import Vuex from 'vuex';
import FastClick from 'fastclick'
import App from './App'
import router from './router'


import $ from 'jquery'

/*import MintUi from 'mint-ui';

import 'mint-ui/lib/style.css';*/

import './assets/render/mui/dist/css/mui.css';

import './assets/css/global.css';

import MyPluginAddConfig from './assets/js/myPlugin.js';

import VueI18n from 'vue-i18n'




import weatherType2IconSm from './assets/js/weathertype2icon.js';

import Title from './components/commons/title.vue';


import 'muse-ui/lib/styles/base.less';
import { Progress,Dialog,Button } from 'muse-ui';
import 'muse-ui/lib/styles/theme.less';

import 'muse-ui-message/dist/muse-ui-message.css';
import Message from 'muse-ui-message';

import scroll from 'vue-seamless-scroll'
Vue.use(scroll)

Vue.use(Message);
Vue.use(Dialog);
Vue.use(Button);
Vue.use(Progress);


import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin)

import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(LoadingPlugin)

Vue.use(ToastPlugin)
Vue.config.productionTip = false

Vue.use(Vuex)

Vue.use(MyPluginAddConfig);

Vue.use(weatherType2IconSm);
Vue.use(VueI18n) 


Vue.component('my-title', Title);
let i18nValue=require('./assets/common/i18n.json');
const i18n = new VueI18n({
locale: 'zh-CN',    // 语言标识

messages: {
'zh-CN': i18nValue.zh,   // 中文语言包
'en-US': i18nValue.en,    // 英文语言包
'pt-PT': i18nValue.pt
}
})




FastClick.attach(document.body)
/* eslint-disable no-new */
window.vm = new Vue({
el: '#app',
i18n,
router,
components: {
App
},
template: '<App/>'
})



